.stars {
  display: flex;
}

.stars__star {
  width: 16px;
  height: 16px;
  margin-right: 4px;
  background: url(../images/star-active.svg) no-repeat center;
}

.stars__star:last-of-type {
  margin-right: 0;
}

.stars--0 .stars__star,
.stars--1 .stars__star:nth-of-type(n+2),
.stars--2 .stars__star:nth-of-type(n+3),
.stars--3 .stars__star:nth-of-type(n+4),
.stars--4 .stars__star:nth-of-type(n+5) {
  background-image: url(../images/star.svg);
}
